import React from 'react';
import { Modal, ModalBody, ModalFooter } from 'reactstrap';

const note = (props, hasBalance) => {
  const msg = props.currency == 'USD' ? "You are scheduling a TX in USD prices. By the time it is fulfilled your available balance may not be enough to cover the request."
    : "The total amount you're requesting is higher than your balance. Make sure to deposit enough ETH to cover the TX amount and reward.";
  return (
    <span><br/>{!hasBalance &&
        <small className="text-danger float-right">
          <i className="fas fa-exclamation-triangle"></i> {msg}
        </small>}
      </span>
  )
}

export default {
  signData: {
    body: ({params}) => {
      const total = params.web3.utils.toBN(params.dataArray.reward).add(params.web3.utils.toBN(params.dataArray.amount));
      const hasBalance = total.lte(params.web3.utils.toBN(params.balance));

      return (
        <ModalBody>
          <h2 className="text-black">TX Request</h2>
          <p>You are about to sign a TX request. This can still be cancelled before someone broadcasts it.</p>
          <h5 className="text-black">Your input data is the following:</h5>
          <p>
            Receiver:<small className="text-monospace float-right"> {params.dataArray.address}</small>
            <br/>Schedule: <span className="float-right">Block {params.dataArray.schedule}</span>
          <br/>TX hash: <span className="text-monospace float-right">{params.web3.utils.hexToAscii(params.hash)}</span></p>
        <hr/>
        <p>Amount: <span className="float-right">{params.web3.utils.fromWei(params.dataArray.amount,'ether')} {params.currency}</span>
        <br/>Reward: <span className="float-right">{params.web3.utils.fromWei(params.dataArray.reward,'ether')} {params.currency}</span></p>
      <hr/>
      <p>Total TX request: <span className="float-right">{params.web3.utils.fromWei(total.toString(),'ether')} {params.currency}</span>
      <br/>Current balance: <span className={"float-right text-" + (hasBalance ? "success" : "danger")}>{params.web3.utils.fromWei(params.balance.toString(),'ether')} ETH</span>
      {note(params, hasBalance)}</p>
        </ModalBody>
      );
    },
    footer: ({toggle, action, params}) => (
      <ModalFooter>
        <button className="btn btn-info" onClick={toggle}>Back</button>
      <button className="btn btn-success" onClick={() => action(params.hash, params.signer, params.dataArray, params.method)}>Sign TX</button>{' '}
    </ModalFooter>
    ),
  },


  cancelData: {
    body: () => (
      <ModalBody>
        <h2 className="text-black">TX Cancellation</h2>
        <p>Cancelling a TX has two steps: Cancelling the request on the blockchain and removing the request from the TXCast server.</p>
        <p>You will be prompted for two signatures:
          <br/>one with a 0.001 ETH cost to cancel on the blockchain,
          <br/>and one with no cost to remove the request.</p>
        <p>It is <b>absolutely necessary</b> to send the 0.001 ETH transaction,
          since that is the only 100% guarantee that your request is fully invalidated. The zero cost transaction will remove your request from the servers, but if someone has a copy of your signature, it can still be used.</p>
      </ModalBody>
    ),
    footer: ({toggle, action, params}) => (
      <ModalFooter>
        <button className="btn btn-info" onClick={toggle}>Back</button>
      <button className="btn btn-danger" onClick={() => action(params.signature, params.nonce)}>Cancel TX</button>{' '}
    </ModalFooter>
    ),
  }
}
