import axios from 'axios';
import values from 'lodash/values';

export async function updateBlock() {
  const latestBlock = await this.web3.eth.getBlock('latest');
  if(latestBlock !== null && /[0-9]*/.test(latestBlock)) this.setState({blockNumber: latestBlock.number});
}

export async function onSign(schedulerInput) {
  let latestBlock = await this.web3.eth.getBlock('latest');
  const dataArray = {
    address: schedulerInput.address,
    amount: this.web3.utils.toWei(schedulerInput.amount.toString(),'ether'),
    reward: this.web3.utils.toWei(schedulerInput.reward.toString(),'ether'),
    byBlocks: true,
    schedule: parseInt(schedulerInput.blocks) + parseInt(latestBlock.number),
    nonce: Math.floor(Math.random() * Number.MAX_SAFE_INTEGER),
    netID: this.state.netID,
    USD: schedulerInput.currency == 'USD',
  };

  const method = schedulerInput.currency == 'USD' ? '0x51004bf5' : '0x392482d1';

  const callData = {
    to: this.addresses[this.state.netID].eths,
    data:'0x03c14526' + this.web3.eth.abi.encodeParameters(this.abiTypes.eths.withoutSignature, values(dataArray)).substr(2),
  }

  const hash = await this.web3.eth.call(callData)
  const signer = this.state.addressList[0];

  const callDataBalance = {
    to: this.addresses[this.state.netID].eths,
    data:'0x70a08231' + this.web3.eth.abi.encodeParameters(
      [
        'address',
      ],
      [signer]
    ).substr(2)
  }

  this.web3.eth.call(callDataBalance).then((b,s) => {
    const balance = this.web3.utils.toBN(b).toString();

    this.setState({
      modal: true,
      params: {hash, signer, dataArray, balance, currency:schedulerInput.currency, method, web3:this.web3},
      innerHTML: this.Modals.signData,
      action: this.signTX
    });
  });
}

export function onBuy() {
  const address = this.state.addressList[0];
  const amount = this.web3.utils.toWei("1",'ether');

  const callDataTX = {
    from: address,
    value: amount, 
    to: this.addresses[this.state.netID].txcsale,
    data:'0xec8ac4d80' + this.web3.eth.abi.encodeParameters(
      [
        'address',
      ],
      [address],
    ).substr(2)
  }

  this.sendTX(callDataTX);
}

export function onCancel(signature, nonce) {
  this.setState({
    modal: true,
    params: {signature, nonce},
    innerHTML: this.Modals.cancelData,
    action: this.cancelTX
  });
}

export function onDeposit(depositInput) {
  const address = this.state.addressList[0];
  const amount = this.web3.utils.toWei(depositInput.amount.toString(),'ether');

  const callDataTX = {
    from: address,
    value: amount, 
    to: this.addresses[this.state.netID].eths,
    data:'0xf340fa01' + this.web3.eth.abi.encodeParameters(
      [
        'address',
      ],
      [address],
    ).substr(2)
  }

  this.sendTX(callDataTX).then(console.log);
}

export function onBalance(balanceInput) {
  const callData = {
    to: this.addresses[this.state.netID].eths,
    data:'0x70a08231' + this.web3.eth.abi.encodeParameters(
      [
        'address',
      ],
      [balanceInput.address]
    ).substr(2)
  }

  this.web3.eth.call(callData).then((b,s) => {
    const balance = this.web3.utils.fromWei(this.web3.utils.toBN(b).toString(), 'ether');
    this.setState({balance: balance})
  });
}

export function onSendRaw(rawInput) {
  const callData = (({address,amount,reward,byBlocks,schedule,nonce,netID,sig}) =>
    ({address,amount,reward,byBlocks,schedule,nonce,netID,sig})) (rawInput.callDataTX);

  const method = rawInput.callDataTX.method;
    
  const callDataTX = {
    to: this.addresses[this.state.netID].eths,
    data: method + this.web3.eth.abi.encodeParameters(
      this.abiTypes.eths.withSignature,
      [...values(callData)]
    ).substr(2),
  }

  this.sendTX(callDataTX);
}

export function onFulfill(dataArray) {
  const callData = (({address,amount,reward,byBlocks,schedule,nonce,netID,sig}) =>
    ({address,amount,reward,byBlocks,schedule,nonce,netID,sig})) (dataArray);

  const method = dataArray.method;

  const callDataTX = {
    to: this.addresses[this.state.netID].eths,
    data: method + this.web3.eth.abi.encodeParameters(
      this.abiTypes.eths.withSignature,
      [...values(callData)]
    ).substr(2),
  }

  this.sendTX(callDataTX).then(() => {
    this.fetchData();
  });
}

export function sendTX(callDataTX) {
  const address = this.state.addressList[0];

  const callDataTXFrom = {...callDataTX, from: address}

  console.log(callDataTXFrom)

  return this.web3.eth.sendTransaction(callDataTXFrom);
}

export function signTX(hash, signer, dataArray, method) {
  this.toggle();

  this.web3.eth.personal.sign(hash, signer)
    .then(signature => {
      const txrequest = {
        method: method,
        signer: signer,
        ...dataArray,
    }

    const formData = new FormData();
    Object.keys(txrequest).forEach(k => {
      formData.set(k,txrequest[k]);
    });

    const req = {
      url: this.urls[this.state.server] + '/signature/' + signature,
      data: formData,
      method: 'PUT',
    };

    axios(req)
      .then((response) => {
        const appended = this.state.queue.slice(0);
        appended[signature] = txrequest;

        console.log(appended)

        this.setState({
          queue: appended,
        }, (s) => console.log);
      })
      .catch((e) => {
        if(e.response){
          console.log("error", e.response.data.message)
        }
      })
      .finally(() => this.fetchData())
  });
}

export function cancelTX(signature, nonce) {
  this.toggle();

  const callDataTX = {
    to: this.addresses[this.state.netID].eths,
    data:'0x8e7baff6' + this.web3.eth.abi.encodeParameters(
      [
        'uint',
      ],
      [ nonce ],
    ).substr(2)
  }

  this.sendTX(callDataTX);

  this.web3.eth.personal.sign("Cancel TX " + nonce, this.state.addressList[0]).then(verified => {
    const cancelOrder = {
      signature: signature,
      nonce: nonce, 
      verified: verified,
    }

    const formData = new FormData();
    Object.keys(cancelOrder).forEach(k => {
      formData.set(k, cancelOrder[k]);
    });

    const req = {
      url: this.urls[this.state.server] + '/signature/' + signature,
      data: formData,
      method: 'DELETE',
    };

    axios(req)
      .then(this.fetchData.bind(this))
      .catch(console.error)
  });
}

