import React, { Component } from 'react';
import { Modal } from 'reactstrap';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

const ActionModal = (props) => (
  <Modal isOpen={props.modal} toggle={props.toggle}>
    {props.innerHTML.body({params:props.params})}
    {props.innerHTML.footer({toggle:props.toggle, action:props.action, params:props.params})}
  </Modal>    
);

class DropdownAppend extends Component {
  constructor(props){
    super(props);
    this.state = {toggle: false, filter: ""};
  }

  setFilter(v) {
    this.setState({...this.state, filter: v});
  }

  toggle = () => {
    this.setState(prevState => ({
      toggle : !prevState.toggle
    }))
  }

  render() {
    return ( 
      <Dropdown className="input-group-append" isOpen={this.state.toggle} toggle={this.toggle}>
        <DropdownToggle caret className="btn-secondary dropdown-toggle">
          {this.props.currency}
        </DropdownToggle>
        <DropdownMenu>
          {this.props.symbols
              .filter(w =>
                w.toUpperCase().search(this.state.filter.toUpperCase()) > -1)
              .map(v => 
                <DropdownItem
                  key={v}
                  href="#"
                  onClick={() => this.props.setCurrency(v)}
                >
                  {v}
                </DropdownItem>)}
        </DropdownMenu>
      </Dropdown>
    )
  }
}

const CollapseButton = (props) => (
  <a onClick={() => props.onCollapse()}>
    <i className={"float-right fas fa-" + (props.collapsed ? "plus" : "minus") + "-square"}></i>
  </a>
);

const ToggleBlocksButton = (props) => (
  <a onClick={() => props.onToggleBlocks()}>
    <i data-tip="Toggle block schedule or approximate UTC time." className={"float-right mr-4 fas fa-toggle-" + (props.blocksFormat ? "on" : "off")}></i>
  </a>
);

const Footer = () => (
  <footer>
    <div className="container">
      <div className="text-center"><i className="fas fa-ellipsis-h"></i></div>
      <div className="row text-center">
        <div className="col-md-12">
          <ul className="list-inline social-buttons">
            <li className="list-inline-item">
              <a href="https://discord.gg/Z2bMx5V" target="_blank">
                <i className="fab fa-discord"></i>
              </a>
            </li>
          </ul>
        </div>
        <div className="col-md-12 py-5">
          <span className="text-white text-monospace">TXCast 2018</span>
        </div>
      </div>
    </div>
  </footer>
);

const PatternBg = () => (
  <svg height="100%" width="100%">
    <defs>
      <pattern id="basicPattern" x="0" y="0" width="60" height="30" patternUnits="userSpaceOnUse">
        <polygon points="0,0 30,15 30,-15"/>
        <polygon points="0,0 30,15 0,30"/>
        <polygon points="0,30 30,15 30,45"/>

        <polygon points="60,0 30,15 30,-15"/>
        <polygon points="60,0 30,15 60,30"/>
        <polygon points="60,30 30,15 30,45"/>
      </pattern>
    </defs>

    <rect x="0" y="0" width="100%" height="100%"
      stroke= "#333333" strokeWidth="1" fill="url(#basicPattern)" />
  </svg>
);

const TXCStatus = (props) => {
  const classColor = props.active ? "text-secondary" : "text-danger";
  const text = props.active ? " Active" : " Inactive";
  const icon = props.active ? "check" : "times";
  return (
    <span className="navbar-text">
      TXC Sub:<span className={classColor}>
        {text}
      </span>
      <i
        data-tip="Active subs are addresses that hold at least 1 TXC and do not pay the 40% fee."
        className={"ml-1 fas fa-" + icon + "-circle " + classColor}
      ></i>
    </span>
  );
};

export { Footer, PatternBg, CollapseButton, ToggleBlocksButton, DropdownAppend, TXCStatus, ActionModal};
